import React, { Component } from 'react'
import './Form.css'

class Form extends Component {

    constructor() {
        super()
        this.state = {
            title: '',
            price: 0,
            quantity: 0,
            imageUrl: ''

        }
    }

    handleInput = (e) => {
        this.setState({
             [e.target.name]: e.target.value    
        })
    }

    previewImage = (event) => {
    
        this.setState({
              imageUrl: URL.createObjectURL(event.target.files[0])  
        })
    }


    add = (e) => {
        e.preventDefault()
        this.props.createProduct(this.state)
        this.setState({
            title: '',
            price: 0,
            quantity: 0,
            imageUrl: ''
        })
    }

    render() {
        return (
            <div className='Form' onSubmit={this.add}>
                <form className='form'>
                    <input type='text' className='input' placeholder='enter the product title' name='title' onChange={this.handleInput} value={this.state.title}/>
                    <input type='number' className='input' placeholder='enter the product price' name='price' onChange={this.handleInput} value={this.state.price}/>
                    <input type='number' className='input' placeholder='enter the product quantity' name='quantity' onChange={this.handleInput} value={this.state.quantity}/>
                    <input type='file' className='input' name='imageUrl' onChange={this.previewImage}/>
                    <button className='add__btn'>Add Product</button>
                </form>   
                
                <div className='form__preview'>
                    <img src={this.state.imageUrl} alt={this.state.title} className='preview__img'/>    
                    <h3 className='form__title'>{this.state.title}</h3>
                    <p className='form__price'>{this.state.price} $</p>
                    <p className='form__quantity'>{this.state.quantity} items</p>
                </div>

            </div>
        )
    }
}


export default Form



