import React, { Component } from 'react'
import Form from './Form'
import Product from './Product'
import './ProductsList.css'

 class ProductsList extends Component {

     constructor(){
         super()
         this.state = {
             products: [],
             search: ''
         }
     }


     createProduct = (product) => {
         this.setState({
             products: [...this.state.products, product]
         })
     }



     handleSearch = (e) => {
         this.setState({
             search: e.target.value
         })   
     }

    render() {
       let filterdProducts = this.state.products.filter(item => {
            return item.title.toLowerCase().includes(this.state.search.toLowerCase());
        })
        return (
            <div>
                <Form createProduct={this.createProduct}/>
                <form className='form__search'>
                    <input type='text' placeholder='search products...' className='search__input' onChange={this.handleSearch}/>
                </form>
                <div className='products'>
                   {filterdProducts.map(product => {
                    return (
                        <Product title={product.title} price={product.price} quantity={product.quantity} img={product.imageUrl}/>
                    )
                })}
                </div>
            </div>
        )
    }
}


export default ProductsList


